.app-title {
    font-family: 'Poppins', sans-serif;
    font-size: 3.6rem;
    text-align: center;
    margin: 1rem auto;
    color: #354F52;
}
.to-do__counter {
    display: flex;
    flex-direction: column;
    color: #84A98C;
    font-family: 'Lora', serif;
    text-align: center;
    font-size: 1.6rem;
    font-weight: 600;
    margin: 1rem auto;
    padding: 1rem;
}
.search-section {
    display: flex;
    flex-direction: column;
    max-width: 320px;
    margin: 1rem auto;
    font-family: 'Lora', serif;
}
.search-section input[type="text"] {
    transition: 300ms;
    font-family: 'Lora', serif;
    font-size: 1.6rem;
    border-radius: 1.6rem;
    color: #84A98C;
    border: 1px solid #CAD2C5;
    padding: 1.2rem 1.6rem;
    outline: none;
}

.search-section input[type="text"]:disabled {
    opacity: .2;
}

.search-section input::-webkit-input-placeholder{
    color: #CAD2C5;
}

.search-section input::placeholder{
    color: #CAD2C5;
}
.to-do-list {
    display: flex;
    flex-direction: column;
    align-items: stretch;
    width: 100%;
    margin: 0 auto;
    justify-content: center;
}
.to-do-list__title {
    font-family: 'Poppins', sans-serif;
    font-size: 2.4rem;
    text-align: center;
    margin: 1rem auto;
    color: #52796F;
}

.to-do-list .loading i {

    font-size: 5rem;
    -webkit-animation: spin 1000ms infinite;
            animation: spin 1000ms infinite;
    color: #d8d8d8;
}

@-webkit-keyframes spin {
    0% {-webkit-transform: rotate(0deg);transform: rotate(0deg);}
    100% {-webkit-transform: rotate(360deg);transform: rotate(360deg);}
}

@keyframes spin {
    0% {-webkit-transform: rotate(0deg);transform: rotate(0deg);}
    100% {-webkit-transform: rotate(360deg);transform: rotate(360deg);}
}

.to-do-list > p {
    justify-self: center;
    font-family: 'Poppins', sans-serif;
    font-size: 2rem;
    margin: 10rem auto;
    color: #c4c4c4;

    font-weight: 300;
}


.to-do-item {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    align-items: center;
    justify-content: flex-start;
    font-family: 'Poppins', sans-serif;
    font-size: 1.6rem;
    margin: 2rem 0;
    padding: 2rem;
    border-radius: 1.6rem;
    box-shadow: 0px 5px 12px #CAD2C5;
}

.to-do-item:hover{
    box-shadow:0px 5px 20px #CAD2C5;
}

.item--complete-button {
    font-size: 2rem;
    cursor: pointer;
}

.to-do-item p {
    transition: 200ms;
    font-weight: light;
    color: #2F3E46;
    flex-grow: 1;
    margin: 0 2rem;
}

.to-do-item i {
    transition: 200ms;
    font-weight: light;
    color: #868b83;
    flex-grow: 1;
}

.to-do-item.completed > p{
    transition: 200ms;
    text-decoration: line-through;
    color: #84A98C;
}

.to-do-item.completed i{
    transition: 200ms;
    color: #84A98C;
}


.item--delete-button {
    font-weight: bolder ;
    color:  #868b83;
    margin: 0 2rem;
    justify-self: flex-end;
    cursor: pointer;
}
.item--delete-button:hover{
    color: #2F3E46;
}


.add-task{
    position: fixed;
    right: 1.5rem;
    bottom: 1.5rem;
    z-index: 2;
}
.add-task__button{
    font-size: 2.8rem;
    border-radius: 50%;
    padding: 1rem 1.8rem;
    border: none;
    outline: none;
    cursor: pointer;
    color: #CAD2C5;
    background-color: #2F3E46;
    box-shadow: 0px 0px 12px #CAD2C5;
    transition: 500ms;
}

.opened{
    background-color: #CAD2C5;
    color: #2F3E46;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
}
.add-task__button:hover{
    box-shadow: 0px 0px 20px #2F3E46;
}
.modal__background{
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;

    height: 100%;

    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #2f3e467f;

}

.modal__card {
    display: flex;
    flex-direction: column;
    align-content: stretch;
    background-color: #ffffff;
    padding: 2rem 4rem;
    border-radius: 1.6rem;
    box-shadow: 0px 5px 12px #CAD2C5;
    -webkit-animation: appear 200ms cubic-bezier(0.215, 0.610, 0.355, 1);
            animation: appear 200ms cubic-bezier(0.215, 0.610, 0.355, 1);
}

@-webkit-keyframes appear {
    from {-webkit-transform: scale(0);transform: scale(0);}
}

@keyframes appear {
    from {-webkit-transform: scale(0);transform: scale(0);}
}


.modal__card .card__title {
    font-family: 'Poppins', sans-serif;
    font-size: 2.8rem;
    text-align: center;
    margin: 1rem auto;
    color: #354F52;
    
}
.modal__card .card__info {
    font-family: 'Poppins', sans-serif;
    font-size: 1.6rem;
    text-align: center;
    margin: 1rem auto;
    color: #4e4e4e;
    max-width: 320px;
}

.modal__card .card__input[type="text"] {
    font-family: 'Lora', serif;
    font-size: 1.6rem;
    border-radius: 1.6rem;
    color: #84A98C;
    border: 1px solid #CAD2C5;
    padding: 1.2rem 1.6rem;
    margin: 1rem;
    outline: none;
    box-shadow: 0px 0px 20px #d9d9d9;
}

.modal__card .card__footer {
    display: flex;
    justify-content: space-around;
    margin: 2rem auto;
}

.modal__card .card__footer button {
    transition: 200ms;
    font-family: 'Poppins', serif;
    font-size: 1.6rem;
    border-radius: 1.6rem;
    border: 1px solid #CAD2C5;
    padding: 1.2rem 2.6rem;
    margin: 1rem;
    outline: none;
    
    cursor: pointer;
}

.modal__card .card__footer .btn-primary{
    background-color: #354F52;
    color: #CAD2C5;
}

.modal__card .card__footer .btn-secondary {
    color: #354F52;
    background-color: #CAD2C5;
}

.modal__card .card__footer button:hover {
    color: #FFF;
}
\--main
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  --color-1: #2F3E46;
  --color-2: #354F52;
  --color-3: #52796F;
  --color-4: #84A98C;
  --color-5: #CAD2C5;
}
html {
  font-size: 62.5%;
}

body {
  display: flex;
  flex-direction: column;
  background-color: #FFF;
  align-items: center;
}
#root{
  display: flex;
  flex-direction: column;
  width: 90%;
  max-width: 1080px;
  min-width: 280px;
}
